import {LOGIN_SUCCESS, LOGIN_FAILURE} from '../constants';

export const loginReducer = function loginReducer(state = {}, action) {
  switch (action.type) {
    case LOGIN_SUCCESS:
      return action.token;
    case LOGIN_FAILURE:
      return action.message;
    default:
      return state;
  }
};



